import React, { Children, cloneElement } from 'react';
import { element, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-avatar';

const AvatarPhoto = ({ children, className, label, type, ...remainingProps }) => {
  if (Children.only(children).type === 'img') {
    return (
      cloneElement(
        children,
        {
          alt: Children.only(children).props.alt || label,
          className: classnames({
            [`${CSS_PREFIX}--photo`]: Boolean(type) === false,
            [`${CSS_PREFIX}--${type}`]: Boolean(type)
          }, className),
          ...remainingProps
        }
      )
    );
  }

  return (
    <div
      className={classnames(`${CSS_PREFIX}--photo`, className)}
      role="img"
      aria-label={label}
      {...remainingProps}
    >
      {
        cloneElement(
          children,
          {
            ...remainingProps
          }
        )
      }
    </div>
  );
};

AvatarPhoto.propTypes = {
  /**
  * Content
  */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * Label
   */
  label: string.isRequired,

  /**
   * Type
   */
  type: oneOf(['company-logo'])
};

AvatarPhoto.displayName = 'AvatarPhoto';

export default AvatarPhoto;
