import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-btn';
const CSS_STATE = 'vds-state';

const Button = forwardRef(({
  active,
  children,
  className,
  disabled,
  flippable,
  ...remainingProps
}, ref) => (
  <button
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--active`]: Boolean(active),
      [`${CSS_STATE}--disabled`]: Boolean(disabled),
      [`${CSS_STATE}--flippable`]: Boolean(flippable)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </button>
));

Button.propTypes = {
  /**
   * Active
   */
  active: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Flippable
   */
  flippable: bool
};

Button.displayName = 'Button';

export default Button;
